
@import "../../assets/scss/fonts";

.champion {
    display: flex;
    margin: auto;
    justify-content: center;
    flex-wrap: wrap;
    flex-direction: column;

}

.headerFont {
    font-family: $primaryfont;
    color: $primaryfontcolor;
};

.winnerStart {
    display: block;
    width: 150px;
    height: 112px;
    background-image: url('../../assets/socials/trophy.svg');
    background-repeat: no-repeat;
}

.winnerPost {
    position: absolute;
    background-position: 0px 0px;
}

.winner {
    font-size: 50px;
}